<template>
    <div class="site-box">
        <Header>
            <van-icon  tag="router-link" to='/my_message' slot="first" name="arrow-left" info="" />
          <p slot="mid"><b>编辑地址</b></p>
          <p slot="last" v-if="!bSys" @click="Show()">编辑</p>
          <p slot="last" v-if="bSys" @click="Show()">完成</p>
        </Header>
        <section class="site-ul">
            <hr/>
            <!-- 新增地址 -->
            <ul class="siet-div-one">
                <li v-for="(item,k) in list" :key="k">
                    <div>
                       <p>{{item.address}}</p>
                        <p>{{item.phone}}</p> 
                    </div>
                    <van-icon tag="div" v-show="bSys" @click="del(k,item.id)" name="cross" />   
                </li>
            </ul>

            <router-link to='/address' tag="div" class="siet-div-tow">
                <p>新增地址</p>
                <van-icon tag="p" name='arrow' />
            </router-link>
        </section>
    </div>
</template>

<script>
import Header from '@/components/header'

export default {
    name:'site-box',
    components:{
        Header,
    },
    data() {
        return {
            list:[],
            arr:{},
            bSys:false,
            
        }
    },
    methods: {
        //获取新地址数据
        async aress(){
            var id=this.$store.state.log.id
            // console.log(id)
            this.axios.get(`http://elm.cangdu.org/v1/users/${id}/addresses`).then((res)=>{
                // console.log(res.data)
                this.list=res.data
                console.log(this.list)
            })
        },
        Show(){
            this.bSys = !this.bSys
        },
        //删除地址
        del(k,req){
            this.list.splice(k,1)
             var id=this.$store.state.log.id
            this.axios.delete(`http://elm.cangdu.org/v1/users/${id}/addresses/${req}`).then((res)=>{
                console.log(res.data)
                this.list=res.data
            })
        }
    },
    mounted() {
        
        //获取vuex中的本地存储
        // console.log(this.$store.state.list)
        // this.arr=this.$store.state.list
        // console.log(this.arr)
        
       this.aress()
    },
}
</script>
<style lang="scss">
p{margin: 0;padding: 0;}
.site-box{
    height:100%;background: #f2f2f2;
    
    }
    .site-ul{
        hr{margin-top: 0.18rem;}
         >.siet-div-tow{ 
              background: #fff;
             display: flex; 
             margin-top: 0.18rem;
             justify-content: space-between;
             align-items: center;
             border: 0.008rem solid #ededed;
             font-size: 0.28rem;
                 p{
                 margin: 0;
                 line-height: 0.7rem;
                padding-left: 0.14rem; 
                padding-right: 0.14rem; 
                }
            p:nth-child(2){
                color: rgb(216, 216, 216);
            }
         }
        .siet-div-one{
            li:nth-of-type(1){
                background: #fff8c3;
            }
            li{
                width: 100%;height: 0.7rem;
                padding: 0.15rem 0;
                display: flex;
                 justify-content: space-between;
                 align-items: center;
                div{
                    padding-right:0.155rem;
                   p{
                        line-height: 0.35rem;
                        padding-left:0.145rem;
                        font-size: 0.22rem; 
                    } 
                }
                
            }
            
        }
    }

</style>


